import React, { useContext } from "react";
import classes from "./NavigationItems.module.scss";
import NavigationItem from "./NavigationItem/NavigationItem";
import { UserContext } from "../../../UserContext";

const NavigationItems = (props) => {
  const data = useContext(UserContext);
  console.log(useContext(UserContext));
  return (
    <ul className={classes.NavigationItems}>
      <NavigationItem link="/" exact>
        
      </NavigationItem>
      <NavigationItem link="/about"> </NavigationItem>
      <NavigationItem link="/product"></NavigationItem>
      <NavigationItem link="/service"></NavigationItem>
      <NavigationItem link="/training"></NavigationItem>
      <NavigationItem link="/kidsclub">Kids Club</NavigationItem>
      <NavigationItem link="/news"></NavigationItem>
      {localStorage.getItem("token") ? (
        <NavigationItem link="/admin"></NavigationItem>
      ) : (
        ""
      )}
    </ul>
  );
};

export default NavigationItems;
